import { useEffect } from 'react'

enum Icons {
  github = 'github',
  linkedin = 'linkedin',
  telegram = 'telegram',
  email = 'email'
}

interface Socials {
  url: string,
  name: string,
  icon: Icons
}

export default function Contact() {
  const socials: Socials[] = [
    {
      url: 'https://github.com/TurniXXD',
      name: 'GitHub',
      icon: Icons.github
    },
    {
      url: 'https://www.linkedin.com/in/jakub-vantuch-552514197/',
      name: 'LinkedIn',
      icon: Icons.linkedin
    },
    {
      url: 'https://t.me/turnix',
      name: 'Telegram',
      icon: Icons.telegram
    },
    {
      url: 'mailto:contact@vantuch.dev',
      name: 'Email',
      icon: Icons.email
    },
  ]

  useEffect(() => {
    const contactLinks = document.getElementsByClassName("contact-link")
    const contactLinkContainers = document.getElementsByClassName("contact-link-container")
    for (let i = 0; i < contactLinkContainers.length; i++) {
      contactLinkContainers[i].addEventListener("mouseover", () => contactLinks[i].classList.add("contact-link-hover"));
      contactLinkContainers[i].addEventListener("mouseout", () => contactLinks[i].classList.remove("contact-link-hover"));
    }
  }, [])

  return (
    <div className="gap-4 sm:grid-cols-3 sm:grid-rows-4 sm:grid sm:h-full">
      <div className="hidden sm:block t-01 l-01 absolute h-8 w-8 rotate-45 border-r-2 border-r-sky-400 bg-grey"></div>
      <div className="grid row-span-2 col-span-1 border-2 border-sky-400">
        <div className="flex-col relative justify-evenly w-full">
          {socials.map((s, i) => (
            <div key={i} className="flex-row justify-start sm:justify-center lg:justify-start w-full px-8 items-center gap-6">
              <img src={`/svg/${s.icon}.svg`} className="scale-75 h-10" />
              <span className="block sm:hidden lg:block contact-link text-base sm:text-lg">{s.name}</span>
            </div>
          ))}
          <div className="absolute flex-col justify-evenly h-full w-full">
            {socials.map((s, i) => (
              <a
                key={i}
                href={s.url}
                target="__blank"
                rel="noreferrer noopener nofollow"
                className="contact-link-container flex-row h-full w-full px-8"
              />
            ))}
          </div>
        </div>
      </div>
      <div className="grid row-span-2 col-span-2 grid-rows-3 h-full gap-4">
        <input
          type="text"
          name="name"
          id=""
          placeholder="Full Name"
          spellCheck="false"
          autoCorrect="off"
          className="h-20 sm:h-full w-full px-16 bg-grey border-2 border-sky-400 outline-none text-xl text-center"
        />
        <input
          type="text"
          name="email"
          id=""
          placeholder='Email'
          spellCheck="false"
          autoCorrect="off"
          className="h-20 sm:h-full w-full px-16 bg-grey border-2 border-sky-400 outline-none text-xl text-center"
        />
        <input
          type="text"
          name="phone"
          id=""
          placeholder="Phone"
          spellCheck="false"
          autoCorrect="off"
          className="h-20 sm:h-full w-full px-16 bg-grey border-2 border-sky-400 outline-none text-xl text-center"
        />
      </div>
      <div className="relative grid row-span-2 col-span-3 grid-rows-3 border-2 border-sky-400">
        <textarea
          name="message"
          id=""
          spellCheck="false"
          autoCorrect="off"
          placeholder="Let me make your dream come true..."
          cols={30}
          rows={10}
          className="outline-none grid row-span-2 text-xl p-10 bg-grey resize-none mb-6"
        />
        <div className="grid absolute bottom-0 right-0 h-28 w-full grid-rows-1 grid-cols-3 row-span-1">
          <button className="contact-button relative grid row-span-1 col-start-2 col-span-2 justify-items-center content-center border-2 border-sky-400 border-r-0 border-b-0">
            <div className="hidden sm:block -top-4 -left-4 absolute h-8 w-8 rotate-45 border-r-2 border-r-sky-400 bg-grey"></div>
            <span className="text-2xl">Send</span>
          </button>
        </div>
      </div>
    </div>
  );
}
